:root {
  --main-text-color: #fcfcfc;
  --burger-menu-color: #fff;
  --secondary-text-color: #131313;
  --main-bodyBg-color: #84a178;
  --nav-hover-color: #f8d24e;
  --iceCream-imgBg-color: linear-gradient( 0deg, rgba(156, 190, 143, .7), rgba(156, 190, 143, .7) ), #d9d9d9;
  --iceCream-bg-color: #aec6a5;
  --iceCoffee-imgBg-color: linear-gradient( 0deg, rgba(248, 210, 78, .7), rgba(248, 210, 78, .7) ), #d9d9d9;
  --iceCoffee-bg-color: #efd478;
  --milkshakes-imgBg-color: linear-gradient( 0deg, rgba(213, 117, 63, .7), rgba(213, 117, 63, .7) ), #d9d9d9;
  --milkshakes-bg-color: #d6936d;
  --cow-bg-color: linear-gradient( 0deg, rgba(248, 210, 78, .7), rgba(248, 210, 78, .7) ), #d9d9d9;
  --address-itemBg-color: #fcfcfc;
  --address-itemBgD-color: #fff;
  --address-titleBg-color: #f8d24e;
  --address-textAd-color: #d6936d;
  --address-time-color: #ababab;
  --address-line-color: #e1e1e1;
  --icon-bg-color: #f8d24e;
  --btn-text-color: #000;
  --btn-border-color: #f8d24e;
  --btn-focus-color: #fff;
  --btn-hover-color: #0a8817;
  --footer-text-color: #7a7a7a;
  --modal-itemI-color: #96af8c;
  --modal-itemC-color: #efd478;
  --modal-itemM-color: #d6936d;
  --time-function: cubic-bezier(.4, 0, .2, 1);
}

body {
  color: var(--main-text-color);
  margin: 0;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

body.modal-open {
  overflow-y: hidden;
}

h1, h2, h3, h4, h5, h6, p, ul, li {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.mobile__list, .footer__list, .contacts__list, .advnt__list, .hero-count__list, .nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.modal-loc__link, .mobile__link, .footer__link, .contacts__address, .contacts__text, .hero__link, .nav__link {
  color: currentColor;
  text-decoration: none;
}

.container {
  background-color: var(--main-bodyBg-color);
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.button {
  fill: currentColor;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
}

.btn-color {
  background-color: var(--btn-border-color);
  color: var(--secondary-text-color);
  border: none;
  justify-content: center;
  align-items: center;
  gap: 4px;
  padding-top: 12px;
  padding-bottom: 12px;
  display: inline-flex;
}

.btn-color:hover {
  background-color: var(--btn-hover-color);
}

.btn-color:focus {
  color: var(--btn-focus-color);
  background-color: var(--btn-hover-color);
}

.btn-transparent {
  border: 2px solid var(--btn-border-color);
  color: var(--btn-text-color);
  background: none;
  padding-top: 12px;
  padding-bottom: 12px;
  font-family: inherit;
}

.btn-transparent:hover {
  border-color: var(--btn-hover-color);
}

.btn-transparent:focus {
  border-color: var(--btn-hover-color);
  color: var(--btn-focus-color);
}

.close-button-modal {
  fill: var(--main-text-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.close-button-modal:hover, .close-button-modal:focus {
  fill: var(--nav-hover-color);
}

