:root {
  --main-text-color: #fcfcfc;
  --burger-menu-color: #ffffff;
  --secondary-text-color: #131313;
  --main-bodyBg-color: #84a178;
  --nav-hover-color: #f8d24e;
  --iceCream-imgBg-color: linear-gradient(
      0deg,
      rgba(156, 190, 143, 0.7),
      rgba(156, 190, 143, 0.7)
    ),
    #d9d9d9;
  --iceCream-bg-color: #aec6a5;
  --iceCoffee-imgBg-color: linear-gradient(
      0deg,
      rgba(248, 210, 78, 0.7),
      rgba(248, 210, 78, 0.7)
    ),
    #d9d9d9;
  --iceCoffee-bg-color: #efd478;
  --milkshakes-imgBg-color: linear-gradient(
      0deg,
      rgba(213, 117, 63, 0.7),
      rgba(213, 117, 63, 0.7)
    ),
    #d9d9d9;
  --milkshakes-bg-color: #d6936d;
  --cow-bg-color: linear-gradient(
      0deg,
      rgba(248, 210, 78, 0.7),
      rgba(248, 210, 78, 0.7)
    ),
    #d9d9d9;
  --address-itemBg-color: #fcfcfc;
  --address-itemBgD-color: #fff;
  --address-titleBg-color: #f8d24e;
  --address-textAd-color: #d6936d;
  --address-time-color: #ababab;
  --address-line-color: #e1e1e1;
  --icon-bg-color: #f8d24e;
  --btn-text-color: #000000;
  --btn-border-color: #f8d24e;
  --btn-focus-color: #fff;
  --btn-hover-color: #0a8817;
  --footer-text-color: #7a7a7a;
  --modal-itemI-color: #96af8c;
  --modal-itemC-color: #efd478;
  --modal-itemM-color: #d6936d;

  --time-function: cubic-bezier(0.4, 0, 0.2, 1);
}

// Media screen
$container-width-mobile: 480px;
$container-width-tablet: 768px;
$container-width-desktop: 1200px;
